
const  isValidUsername =(str) =>{
  return ['admin', 'editor'].indexOf(str.trim()) >= 0;
}

const  isExternal =(path) =>{
  return /^(https?:|mailto:|tel:)/.test(path);
}

const  isCellPhone =(val) =>{
  if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
    return false
  } else {
    return true
  }
}

//
export const  checkUserName= (rule, value, callback)=>{
  if (value == "") {
    callback(new Error(""))
  } else if (value.length > 20 || value.length <3) {
    callback(new Error("3-20"))
  } else {
    callback()
  }
}

//
export const  checkName =(rule, value, callback)=>{
  if (value == "") {
    callback(new Error(""))
  } else if (value.length > 12) {
    callback(new Error("1-12"))
  } else {
    callback()
  }
}

export const checkPhone =(rule, value, callback)=>{
  // let phoneReg = /(^1[3|4|5|6|7|8|9]\d{9}$)|(^09\d{8}$)/;
  if (value == "") {
    callback(new Error(""))
  } else if (!isCellPhone(value)) {//methods
    callback(new Error("!"))
  } else {
    callback()
  }
}


export const validID =(rule, value, callback)=> {
  // 1518151817X
  let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
  if(value == '') {
    callback(new Error(''))
  } else if (reg.test(value)) {
    callback()
  } else {
    callback(new Error(''))
  }
}
export default {
  checkUserName,checkName,checkPhone,validID
}
