import TestRequst from "@/js/BackendRequst";
// 
export function getDishPage(params) {
  return TestRequst({
    url: '/dish/page',
    method: 'get',
    params
  })
}

// 
export function deleteDish  (ids) {
  return TestRequst({
    url: '/dish',
    method: 'delete',
    params: { ids }
  })
}

// 
export function editDish  (params) {
  return TestRequst({
    url: '/dish',
    method: 'put',
    data: { ...params }
  })
}

// 
export function addDish  (params) {
  return TestRequst({
    url: '/dish',
    method: 'post',
    data: { ...params }
  })
}

// 
export function queryDishById  (id) {
  return TestRequst({
    url: `/dish/${id}`,
    method: 'get'
  })
}

// 
export function getCategoryList1(params) {
  return TestRequst({
    url: '/category/list',
    method: 'get',
    params
  })
}

// 
export function queryDishList  (params) {
  return TestRequst({
    url: '/dish/list',
    method: 'get',
    params
  })
}

// down
export function commonDownload  (params) {
  return TestRequst({
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded; charsetUTF-8'
    },
    url: '/common/download',
    method: 'get',
    params
  })
}

// ---
export function dishStatusByStatus  (params) {
  return TestRequst({
    url: `/dish/status/${params.status}`,
    method: 'post',
    params: { ids: params.id }
  })
}
