import axios from 'axios'
import Vue from 'vue'; //  Vue
import store from "@/store";
const instance = axios.create({
  // baseURL: 'http://123.60.129.35:8081/api',
  baseURL: '/api',
  timeout:16000,
});
const source = axios.CancelToken.source();
instance.interceptors.request.use(
  (config) => {
    const jwtToken = localStorage.getItem('dengliObj');
    // 
    config.headers['jwtToken'] = jwtToken;
    //id
    const userInfo = JSON.parse(localStorage.getItem('userInfo'));
    if (userInfo && userInfo.id) {
      console.log(userInfo.id);
      //id,id
      config.headers['Employee'] = userInfo.id;
      config.headers['mendian'] = userInfo.mendianId;
    }else {
      //
      //
      // Vue.prototype.$message({
      //   message: "",
      //   type: 'error',
      //   duration: 1000,
      // })
      // //  2,
      // setTimeout(() => {
      //   window.requestAnimationFrame(()=>{
      //     window.location.href= '#/login'
      //   })
      // }, 1000);
      // // 
      // source.cancel('No userInfo available');
    }
    // 
    store.commit('updateloading',true)
    // this.$store.commit('updateloading',true)
    return config;
  },
  (error) => {
    console.log(error);
    return Promise.reject(error);
  }
);

instance.interceptors.response.use(
  (response) => {
    // 
    const code = response.data.code;
    const msg = response.data.msg;
    store.commit('updateloading',false)
    if (msg == 'NOT_LOGIN') {
      localStorage.removeItem('userInfo');
      //
       Vue.prototype.$message({
          message: "",
          type: 'error',
          duration: 1000,
        })
      //  2,
      setTimeout(() => {
        window.requestAnimationFrame(()=>{
          window.location.href= '#/login'
        })
      }, 1000);
      store.commit('updateloading',false)
      return response.data;
    } else {
      store.commit('updateloading',false)
      return response.data;
    }
  },
  (error) => {
    console.log('err' + error);
    let { message } = error;
    if (message == 'Network Error') {
      message = '';
    } else if (message.includes('timeout')) {
      message = '';
    } else if (message.includes('Request failed with status code')) {
      message = '' + message.substr(message.length - 3) + '';
    }

    // 
    Vue.prototype.$message({
      message: message,
      type: 'error',
      duration: 5 * 1000,
    });
    return Promise.reject(error);
  }
);
export default instance
