import FrontRequst from "@/js/FrontRequst";

//
export function categoryListApi() {
    return FrontRequst({
      'url': '/category/list',
      'method': 'get',
    })
  }

//
export function dishListApi(data) {
    return FrontRequst({
        'url': '/dish/list',
        'method': 'get',
        params:{...data}
    })
}

//
export function setmealListApi(data) {
    return FrontRequst({
        'url': '/setmeal/list',
        'method': 'get',
        params:{...data}
    })
}

//
export function cartListApi(data) {
    return FrontRequst({
        'url': '/shoppingCart/list',
        'method': 'get',
        params:{...data}
    })
}

//
export function  addCartApi(data){
    return FrontRequst({
        'url': '/shoppingCart/add',
        'method': 'post',
        data
      })
}

//
export function  updateCartApi(data){
    return FrontRequst({
        'url': '/shoppingCart/sub',
        'method': 'post',
        data
      })
}

//
export function clearCartApi() {
    return FrontRequst({
        'url': '/shoppingCart/clean',
        'method': 'delete',
    })
}

//
export function setMealDishDetailsApi(id) {
    return FrontRequst({
        'url': `/setmeal/dish/${id}`,
        'method': 'get',
    })
}


