<template>
  <div class="dashboard-container" id="combo-app">
    <div class="container">
      <div class="tableBar">
        <el-input
          v-model="input"
          placeholder=""
          style="width: 250px"
          clearable
          @keyup.enter.native="handleQuery"
        >
          <i
            slot="prefix"
            class="el-input__icon el-icon-search"
            style="cursor: pointer"
            @click="init"
          ></i>
        </el-input>
        <div class="tableLab">
          <span class="span-btn delBut non" @click="deleteHandle('')"></span>
          <span class="span-btn blueBug non" @click="statusHandle('1')"></span>
          <span
            style="border:none;"
            class="span-btn delBut non"
            @click="statusHandle('0')"
          ></span>
          <el-button
            type="primary"
            @click="addSetMeal('add')"
          >
            + 
          </el-button>
        </div>
      </div>
      <el-table
        :data="tableData"
        stripe
        class="tableBox"
        @selection-change="handleSelectionChange"
        v-loading=this.$store.state.obj.loading
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
      >
        <el-table-column
          type="selection"
          width="25"
        ></el-table-column>
        <el-table-column
          prop="name"
          label=""
        ></el-table-column>
        <el-table-column prop="image" label=""  align="center">
          <template slot-scope="{ row }">
            <el-image style="width: auto; height: 40px; border:none;cursor: pointer;" :src="getImage(row.image)" :preview-src-list="[ `api/common/download?name=${row.image}` ]" >
              <div slot="error" class="image-slot">
                <img src="../../../images/noImg.png" style="width: auto; height: 40px; border:none;" >
              </div>
            </el-image>
          </template>
        </el-table-column>
        <el-table-column
          prop="categoryName"
          label=""
        ></el-table-column>
        <el-table-column
          prop="price"
          label=""
        >
          <template slot-scope="scope">
            <span>{{ scope.row.price/100 }}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.status == '0' ? '' : '' }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="updateTime"
          label=""
        >
        </el-table-column>
        <el-table-column
          label=""
          width="160"
          align="center"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              class="blueBug"
              @click="addSetMeal(scope.row.id)"
            >
              
            </el-button>
            <el-button
              type="text"
              size="small"
              class="blueBug"
              @click="statusHandle(scope.row)"
            >
              {{ scope.row.status == '0' ? '' : '' }}
            </el-button>
            <el-button
              type="text"
              size="small"
              class="delBut non"
              @click="deleteHandle('', scope.row.id)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        class="pageList"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="counts"
        @size-change="handleSizeChange"
        :current-page.sync="page"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import { getSetmealPage, deleteSetmeal, setmealStatusByStatus,addSetmeal,editSetmeal,querySetmealById} from '@/api/backend/combo';
import {rootpanduan} from "@/api/utils";

export default {
  name: 'ComboIndex',
  data() {
    return {
      input: '',
      counts: 0,
      page: 1,
      pageSize: 10,
      tableData: [],
      dishState: '',
      checkList: [],
    };
  },
  created() {
    this.init();
  },
  methods: {
    async init() {
      const params = {
        page: this.page,
        pageSize: this.pageSize,
        name: this.input ? this.input : undefined,
      };
      await getSetmealPage(params)
        .then((res) => {
          if (String(res.code) === '1') {
            this.tableData = res.data.records || [];
            this.counts = res.data.total;
          }
        })
        .catch((err) => {
          this.$message.error('' + err);
        });
    },
    getImage(image) {
      return `/api/common/download?name=${image}`;
    },
    handleQuery() {
      this.page = 1;
      this.init();
    },
    //
    addSetMeal(st) {
      if (rootpanduan()) {
        return;
      }
      if (st === 'add'){
        //
        this.$store.commit("updateflag",2)
        this.$store.commit("updateid",0)
        console.log("")
        this.$store.commit("update","ComboAdd")
      } else {
        //
        //storeid
        this.$store.commit("updateid",st)
        console.log(this.$store.state.obj.pathid)
        this.$store.commit("update","ComboAdd")
      }
    },
    deleteHandle(type, id) {
      if (rootpanduan()) {
        return;
      }
      if (type === '' && id === null) {
        if (this.checkList.length === 0) {
          return this.$message.error('');
        }
      }
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
      })
        .then(() => {
          deleteSetmeal(type === '' ? this.checkList.join(',') : id)
            .then((res) => {
              if (res.code === 1) {
                this.$message.success('');
                this.handleQuery();
              } else {
                this.$message.error(res.msg || '');
              }
            })
            .catch((err) => {
              this.$message.error('' + err);
            });
        })
        .catch(() => {});
    },
    statusHandle(row) {
      if (rootpanduan()) {
        return;
      }
      let params = {};
      if (typeof row === 'string') {
        if (this.checkList.length == 0) {
          this.$message.error('');
          return false;
        }
        params.ids = this.checkList.join(',');
        params.status = row;
      } else {
        params.ids = row.id;
        params.status = row.status ? '0' : '1';
      }
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          setmealStatusByStatus(params)
            .then((res) => {
              if (res.code === 1) {
                this.$message.success('');
                this.handleQuery();
              } else {
                this.$message.error(res.msg || '');
              }
            })
            .catch((err) => {
              this.$message.error('' + err);
            });
        })
        .catch(() => {});
    },
    handleSelectionChange(val) {
      if (rootpanduan()) {
        return;
      }
      let checkArr = [];
      val.forEach((n) => {
        checkArr.push(n.id);
      });
      this.checkList = checkArr;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.init();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.init();
    },
  },
};
</script>

<style lang="less" scoped>
//@import "../../styles/common.css";
@import "@/styles/backend/page.css";;
/*  */
</style>
