import TestRequst from "@/js/BackendRequst";

// 
export function getSetmealPage  (params)  {
  return TestRequst({
    url: '/setmeal/page',
    method: 'get',
    params
  })
}

// 
export function deleteSetmeal  (ids)  {
  return TestRequst({
    url: '/setmeal',
    method: 'delete',
    params: { ids }
  })
}

// 
export function editSetmeal  (params)  {
  return TestRequst({
    url: '/setmeal',
    method: 'put',
    data: { ...params }
  })
}

// 
export function addSetmeal  (params)  {
  return TestRequst({
    url: '/setmeal',
    method: 'post',
    data: { ...params }
  })
}

// 
export function querySetmealById  (id)  {
  return TestRequst({
    url: `/setmeal/${id}`,
    method: 'get'
  })
}
export function getCategoryList(params) {
  return TestRequst({
    url: '/category/list',
    method: 'get',
    params
  })
}
// 
export function setmealStatusByStatus  (params)  {
  return TestRequst({
    url: `/setmeal/status/${params.status}`,
    method: 'post',
    params: { ids: params.ids }
  })
}
