import TestRequst from "@/js/BackendRequst";
// 
export function getOrderDetailPage (params) {
  return TestRequst({
    url: '/order/page',
    method: 'get',
    params
  })
}

// 
export function queryOrderDetailById (id) {
  return TestRequst({
    url: `/orderDetail/${id}`,
    method: 'get'
  })
}

// 
export function editOrderDetail (params) {
  return TestRequst({
    url: '/order',
    method: 'put',
    data: { ...params }
  })
}
