<template>
  <div id="address_edit" class="app">
    <div class="divHead">
      <div class="divTitle">
        <i class="el-icon-arrow-left" @click="goBack"></i>{{title}}
      </div>
    </div>
    <div class="divContent">
      <div class="divItem">
        <span></span>
        <el-input placeholder=" " v-model="form.consignee"  maxlength='10' class="inputUser"/>
        <span class="spanChecked" @click="form.sex = '1'">
                    <i :class="{iActive:form.sex === '1'}"></i>
                    
                   </span>
        <span class="spanChecked" @click="form.sex = '0'">
                    <i :class="{iActive:form.sex === '0'}"></i>
                    
                </span>
      </div>
      <div class="divItem">
        <span></span>
        <el-input placeholder=" " v-model="form.phone"  maxlength='20' style="width: calc(100% - 80rem);"/>
      </div>
      <div class="divItem">
        <span></span>
        <el-input placeholder=" " v-model="form.detail"  maxlength='140'/>
      </div>
      <div class="divItem ">
        <span></span>
        <span v-for="(item,index) in labelList" :key="index" @click="form.label = item;activeIndex = index" :class="{spanItem:true,spanActiveSchool:activeIndex === index}">{{item}}</span>
      </div>
      <div class="divSave" @click="saveAddress"></div>
      <div class="divDelete" @click="deleteAddress" v-if="id"></div>
    </div>
  </div>
</template>

<script>
import setRem from "@/js/base";
import {addAddressApi,addressFindOneApi,addressLastUpdateApi,addressListApi,deleteAddressApi,getDefaultAddressApi,setDefaultAddressApi,updateAddressApi} from "@/api/front/address"
import {parseUrl} from "@/utils/common";
export default {
  name: "AddressEdit",
  data(){
    return {
      title:'',
      form:{
        consignee:'',//
        phone:undefined,//
        sex:'1',//0 1 
        detail:'',//
        label:'',//
      },
      labelList:[
        '','','',''
      ],
      id:undefined,
      activeIndex :0
    }
  },
  computed:{},
  created(){
    setRem(document, window);
    this.initData()
  },
  mounted(){
  },
  //
  beforeDestroy(){
    //id
    this.$store.commit("updateAddressid",0)
  },
  methods:{
    goBack(){
      history.go(-1)
    },
    async initData(){
      const params = this.$store.state.ObjPhone
      this.id = params.id;
      console.log("id")
      console.log(params.id)
      if(params.id){
        this.title = ''
        const res = await addressFindOneApi(params.id)
        if(res.code === 1){
          this.form = res.data
        }else{
          this.$notify({ type:'warning', message:res.msg});
        }
      }
    },
    async saveAddress(){
      const form = this.form
      if(!form.consignee){
        this.$notify({ type:'warning', message:''});
        return
      }
      if(!form.phone){
        this.$notify({ type:'warning', message:''});
        return
      }
      if(!form.detail){
        this.$notify({ type:'warning', message:''});
        return
      }
      const reg = /^1[3|4|5|7|8][0-9]{9}$/
      if(!reg.test(form.phone)){
        this.$notify({ type:'warning', message:''});
        return
      }
      let res= {}
      if(this.id){
        res = await updateAddressApi(this.form)
      }else{
        res = await addAddressApi(this.form)
      }

      if(res.code === 1){
        window.requestAnimationFrame(()=>{
          window.location.replace('#/front/page/address')
        })
      }else{
        this.$notify({ type:'warning', message:res.msg});
      }
    },
    deleteAddress(){
      this.$dialog.confirm({
        title: '',
        message: '',
      })
        .then( async () => {
          const res = await deleteAddressApi({ids:this.id })
          if(res.code === 1){
            window.requestAnimationFrame(()=>{
              window.location.replace('#/front/page/address')
            })
          }else{
            this.$notify({ type:'warning', message:res.msg});
          }
        })
        .catch(() => {
        });
    },
  }
}
</script>



<style scoped lang="less">
@import "@/styles/front/address-edit.css";

</style>
