// import request from "@/utils/request";
// import axiosPlugin from "@/js/request";
import TestRequst from "@/js/BackendRequst";
import {request} from "axios";
// 
export function getMemberList(params) {
  return TestRequst({
    url: '/employee/page',
    method: 'get',
    params
  })
}

// 
export function enableOrDisableEmployee(params) {
  return TestRequst({
    url: '/employee',
    method: 'put',
    data: { ...params }
  })
}

// 
export function addEmployee(params) {
  return TestRequst({
    url: '/employee',
    method: 'post',
    data: { ...params }
  })
}

// 
export function editEmployee(params) {
  return TestRequst({
    url: '/employee',
    method: 'put',
    data: { ...params }
  })
}

// ID
export function queryEmployeeById(id) {
  return TestRequst({
    url: `/employee/${id}`,
    method: 'get'
  })
}

// 
export const addCart = (goodsId, goodsNum, goodsSkuId) => {
  return TestRequst.post('/cart/add', {
    goodsId,
    goodsNum,
    goodsSkuId
  })
}
