<template>
  <div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="form.region" placeholder="" style="width: 60%">
            <el-option v-for="item in fromdata" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="close"> </el-button>
        <el-button type="primary" @click="trueflag()"> </el-button>
      </div>
    </el-dialog>
  </div>

</template>
<script>
import {getmendianPage} from "@/api/front/mendian";
import {getOrderDetailPage} from "@/api/backend/order";

export default {
  name: "selectIndex",
  data(){
    return{
      dialogFormVisible: true,
      form: {
        region: '',
        type: [],
        resource: '',
        desc: ''
      },
      formLabelWidth: '120px',
      fromdata:[]
    }
  },

  async created() {
    const parames={
      id:JSON.parse(localStorage.getItem("userInfo")).id
    }
     await getmendianPage(parames).then(res=>{
      if (String(res.code) === '1') {
        this.fromdata =res.data
      }
    }).catch(err => {
      this.$message.error('' + err)
    });
    this.$alert('root', '', {
      confirmButtonText: '',
      callback: action => {
      }
    });
  },
  methods:{
    //
    trueflag(){
      this.dialogFormVisible=false
      const flag=JSON.parse(localStorage.getItem("userInfo"))
      flag.mendianId=this.form.region
      //
      localStorage.removeItem("userInfo")
      //
      localStorage.setItem("userInfo",JSON.stringify(flag))
      this.$router.push("/backend")
    },
    close(){
      this.dialogFormVisible=false
      this.$message.error('')
      this.$router.push("/login")
    },
    handleClose(done) {
      this.$confirm('')
        .then(_ => {
          done();
        })
        .catch(_ => {});
    }
  }
}
</script>
<style scoped lang="less">

</style>
